<template>
  <div>
    <!-- <div>stringifyCurrentPageIds : {{ stringifyCurrentPageIds }}</div> -->
  </div>
</template>

<script>
export default {
  name: "home-project-state-loader",

  computed: {
    stringifyCurrentPageIds() {
      return this.$store.getters["project/stringifyCurrentPageIds"];
    },
  },

  watch: {
    stringifyCurrentPageIds(value) {
      this.$store.dispatch("project/fetchByIds", { value });
    },
  },
};
</script>
