<template>
  <HomeFrame :config="theFrameConfig" @action="handleAction">
    <div class="box-outer">
      <div class="box-inner">
        <MyFilter class="clientbox" />
        <MyPager class="clientbox" />
        <MyTable class="clientbox" />
        <MyStateLoader />
      </div>
    </div>
  </HomeFrame>
</template>

<script>
import MyTable from "./table.vue";
import MyPager from "./pager.vue";
import MyFilter from "./filter.vue";
import MyStateLoader from "./state-loader.vue";

export default {
  name: "home-projects-index",

  components: { MyTable, MyPager, MyFilter, MyStateLoader },

  computed: {
    theFrameConfig() {
      return {
        pager: false,
        home: true,
        nav: true,
        search: false,
        searchCategory: "工程项目",
      };
    },

    // theCountItemsTotal() {
    //   return this.$store.getters["local_repository/total"];
    // },

    // theItems() {
    //   return this.$store.getters["local_repository/currentPageItems"];
    // },
  },

  data() {
    return {
      // pageSize: 1,
      // currentPage: 1,
      // background: "",
      // displayOpenItemDialog: false,
      // dataForOpenItemDialog: {},
    };
  },

  methods: {
    handleAction(p) {
      let name = p.name;
      if (name == "refresh") {
        this.fetch();
      } else if (name == "add") {
        this.add();
      } else if (name == "applyFilter") {
        this.applyFilter(p.value);
      } else if (name == "open-item-more") {
        this.openItemMore(p);
      } else {
        console.warn("unsupported action: " + name);
      }
    },

    fetch() {
      this.$store.dispatch("local_repository/fetch");
    },
  },

  mounted() {
    // this.pageModelToView();
    // this.initOpenWithList();
    this.fetch();
  },
};
</script>

