<template>
  <div>
    <SearchBox ref="theSearchBox" :config="config" @action="handleAction" />
  </div>
</template>

<script>
import SearchBox from "@/components/frame/home-frame/search-box.vue";

function nop() {}

export default {
  name: "home-project-filter",

  components: { SearchBox },

  data() {
    const config = {
      searchCategory: "工程项目",
    };
    return {
      config,
    };
  },

  methods: {
    handleAction(e) {
      let name = e.name;
      if (name == "applyFilter") {
        this.handleApplyFilter(e);
      }
    },

    handleApplyFilter(e) {
      let filter = e.value;
      this.$store.commit("project/update", { filter });
    },

    init(text) {
      nop(text);
      //   this.$refs.theSearchBox.applyFilter(text);
    },
  },

  mounted() {
    this.init("abc123");
  },
};
</script>
